/**
 * M4B Chapter Extractor - Extracts chapter information from M4B files
 * Similar to how VLC media player reads chapter data from MP4/M4B files
 */

export async function extractM4BChapters(file) {
    try {
        const buffer = await file.arrayBuffer();
        const view = new DataView(buffer);
        
        // Find the moov atom which contains chapter information
        const moovOffset = findAtom(view, 'moov', 0);
        if (moovOffset === -1) {
            console.log('No moov atom found in file');
            return [];
        }
        
        // Look for the trak atom inside moov
        const trakOffset = findAtom(view, 'trak', moovOffset);
        if (trakOffset === -1) {
            console.log('No trak atom found');
            return [];
        }
        
        // Look for chapters in the file
        // Chapters can be in the 'chpl' atom or in specific ilst atoms
        
        // Method 1: Try to find userdata with chapter list
        const udatOffset = findAtom(view, 'udat', moovOffset);
        if (udatOffset !== -1) {
            const chplOffset = findAtom(view, 'chpl', udatOffset);
            if (chplOffset !== -1) {
                return parseChplAtom(view, chplOffset);
            }
        }
        
        // Method 2: Try to find chapters in the general moov structure
        return parseChaptersFromMoov(view, moovOffset);
        
    } catch (error) {
        console.error('Error extracting M4B chapters:', error);
        return [];
    }
}

function findAtom(view, atomName, startOffset = 0) {
    const atomCode = stringToByteArray(atomName);
    const searchLimit = Math.min(view.byteLength, startOffset + 100000000); // Limit search to first 100MB
    
    for (let i = startOffset; i < searchLimit - 3; i++) {
        let found = true;
        for (let j = 0; j < 4; j++) {
            if (view.getUint8(i + 4 + j) !== atomCode[j]) {
                found = false;
                break;
            }
        }
        if (found) {
            return i;
        }
    }
    return -1;
}

function stringToByteArray(str) {
    const bytes = [];
    for (let i = 0; i < str.length; i++) {
        bytes.push(str.charCodeAt(i));
    }
    return bytes;
}

function parseChplAtom(view, offset) {
    try {
        // chpl atom structure:
        // size (4 bytes) + 'chpl' (4 bytes) + version (1 byte) + flags (3 bytes) + entry count (1 byte)
        if (offset + 12 >= view.byteLength) return [];
        
        const entryCount = view.getUint8(offset + 12);
        
        const chapters = [];
        let currentOffset = offset + 13;
        
        for (let i = 0; i < entryCount && currentOffset < view.byteLength; i++) {
            // Each chapter entry: start time (4 bytes) + title length (1 byte) + title (variable)
            if (currentOffset + 5 > view.byteLength) break;
            
            const startTime = view.getUint32(currentOffset, false) / 1000; // Convert to seconds
            const titleLength = view.getUint8(currentOffset + 4);
            currentOffset += 5;
            
            if (currentOffset + titleLength > view.byteLength) break;
            
            const titleBytes = new Uint8Array(view.buffer, currentOffset, titleLength);
            const title = new TextDecoder().decode(titleBytes);
            currentOffset += titleLength;
            
            chapters.push({
                title: title || `Chapter ${i + 1}`,
                startTime: startTime,
                duration: null
            });
        }
        
        if (chapters.length > 0) {
            console.log(`Extracted ${chapters.length} chapters from chpl atom`);
        }
        return chapters;
        
    } catch (error) {
        console.error('Error parsing chpl atom:', error);
        return [];
    }
}

function parseChaptersFromMoov(view, moovOffset) {
    // This is a fallback parser that looks through the moov structure
    // for chapter information in various formats
    const chapters = [];
    
    try {
        // Get moov size to know search bounds
        if (moovOffset < 4) return [];
        const moovSize = view.getUint32(moovOffset - 4, false);
        const moovEnd = moovOffset + moovSize;
        
        // Search for chapter markers within moov
        for (let i = moovOffset; i < moovEnd - 100; i++) {
            // Look for common chapter patterns
            if (view.getUint32(i, false) === 0x6368706c || // 'chpl' in reverse endian
                view.getUint32(i, false) === 0x6c706863) {  // 'lphc' 
                const chapterData = parseChplAtom(view, i);
                if (chapterData.length > 0) {
                    return chapterData;
                }
            }
        }
    } catch (error) {
        console.error('Error parsing moov for chapters:', error);
    }
    
    return chapters;
}

export function formatTime(seconds) {
    if (!seconds || isNaN(seconds)) return '0:00';
    const hrs = Math.floor(seconds / 3600);
    const mins = Math.floor((seconds % 3600) / 60);
    const secs = Math.floor(seconds % 60);
    
    if (hrs > 0) {
        return `${hrs}:${mins.toString().padStart(2, '0')}:${secs.toString().padStart(2, '0')}`;
    }
    return `${mins}:${secs.toString().padStart(2, '0')}`;
}
